<template>
  <div>
    <h1>Lista de videos</h1>
    <div class="bloque">
        <article class="bloque__article" v-for='(db,id) in dbs' :key="id">
            <div class="bloque__article__options">                 
                <button title="delete" href="#" class="borrar-video" data-id=${perro.id} @click="eliminar">X</button>
            </div>
            <div class="bloque__article__img">
                <iframe width="450" height="315"
            :src="db.urlVideo">
            </iframe>
            </div>
            <div class="bloque__article__mainInfo">
                <p class="title">{{db.titulo}}</p>
                <p class="desc">{{db.descripcion}}</p>
            </div>
            <div>
                <router-link :to="`/${db.id}`">
                    <button class="verDetalle">Ver Detalle</button>
                </router-link>
            </div>
        </article>
    </div>
  </div>
</template>

<script>
export default {
    name: 'Home',
    data() {
        return{
            dbs:[]  
        };
    },
    methods:{
        async getVideos(){
            const info = await fetch(`http://localhost:3000/posts`);
            const data = await info.json();

            this.dbs = data;

            console.log(data);
        },

        eliminar(){            
            const bloque = document.querySelector(".bloque");

            bloque.addEventListener("click", eliminarVideo);

            function eliminarVideo(e) {

                e.preventDefault();
                if (e.target.classList.contains("borrar-video")) {
                    const videoActual = e.target.parentElement;
                    
                    const videoID = videoActual.querySelector("button").getAttribute("data-id");

                    e.target.parentElement.parentElement.remove();
                }
            }
        }
    },
  
    created(){
      this.getVideos();
    },
}

</script>

